body {
  background: $dark-050;
  overflow-x: hidden;
}

.sidebar {
  @media (max-width: $break-md) {
    display: none;
  }
}

.main {
  max-width: $site-max-width;
  margin: 0 auto;
  background: $white;

  &__wrapper {
    display: flex;
    max-width: $content-max-width;
    margin: 0 auto;

    @media (max-width: $break-md) {
      padding-left: $wrapper-padding;
      padding-right: $wrapper-padding;
    }
  }
}
